body{
  background-color: #000;
  height: 100vh;
}
.App{  
  margin: 50px 30px;
  padding: 25px 15px;
  border: 3px solid #ffffff;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  /* width: 150px;
  height: 150px; */
}
.container{
  display: flex;
  flex-direction: column;
}
.container ul li{
  width: 170px;
  overflow-wrap: break-word;
  list-style-type: "ðŸš€";
  color: #fff;
}
h2{
  text-align: center;
  color: #fff;
}
.done{
   text-decoration: red line-through;
}
.item{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
span{
  cursor: pointer;
}
input{
  color: red;
  font-weight: bold;
  margin-bottom: 11px;
  background-color: #1d0640;
  outline: none;
  border: none;
  height: 25px;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  padding-left: 10px;
}

button{
  height: 35px;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  border: none;
  outline: none;
  color: aqua;
  background-color: #190028;
  font-weight: bold;
 
}
button:hover{
  transition: 1s ease-in-out;
  color: #190028;
  background-color: aqua;
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -ms-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
}